  position: absolute;
  background: rgba(255, 255, 255, 0.15); }

.os-theme-round-light > .os-scrollbar-horizontal > .os-scrollbar-track:before {
  left: 3px;
  right: 3px;
  height: 2px;
  top: 50%;
  margin-top: -1px; }

.os-theme-round-light > .os-scrollbar-vertical > .os-scrollbar-track:before {
  top: 3px;
  bottom: 3px;
  width: 2px;
