.os-theme-round-light > .os-scrollbar > .os-scrollbar-track > .os-scrollbar-handle {
  background: transparent; }

.os-theme-round-light > .os-scrollbar > .os-scrollbar-track > .os-scrollbar-handle:before {
  content: '';
