.rt-site-footer {
    position: relative;
    margin-top: 70px;
    @include breakpoint(md){
        margin-top: 50px;
    }
    .footer-top {
        padding-top: 100px;
        background-size: 100% 100%;
        background-repeat: no-repeat;
        padding-bottom: 90px;
        @include breakpoint(md){
            padding-top: 60px;
            padding-bottom: 30px;
        }
        @include breakpoint(xs){
            background-image: none !important;
            padding-top: 50px;
            background-color: var(--secondary_color);
        }
        .inner-top {
            h4 {
                font-size: 40px;
                font-weight: 600;
                line-height: 50px;
                margin-bottom: 15px;
                @include breakpoint(md){
                    font-size: 36px;
                    line-height: 48px;
                }
                @include breakpoint(xs){
                    font-size: 26px;
                    line-height: 36px;
                }
            }
            .row {
                padding-bottom: 105px;
                border-bottom: 1px solid rgb(77, 107, 203);
                margin-bottom: 120px;
                @include breakpoint(md){
                    padding-bottom: 60px;
                    margin-bottom: 80px;
                    padding-top: 60px;
                }
                @include breakpoint(xs){
                    padding-bottom: 30px;
                    padding-top: 30px;
                    margin-bottom: 40px;
                }
            }
        }
    }
    .footer-bottom {
        padding: 15px 0px;
        background-color: #03238d;
    }
}

.rt-single-widget {
    margin-bottom: 30px;
    color: #fff;
    .badge-doted{
        padding-bottom: 30px;
        margin-bottom: 30px;
        border-bottom: 2px dotted #7d9cff;
    }
    .social-icons{
        li{
            margin-right: 7px;
            &:last-child{
                margin-right: 0px;
            }
        }
    }
    h4{
        font-size: 24px;
        margin-bottom: 30px;
        color: #fff;
    }
    
    a {
        color: var(--primary_color);
    }
    .rt-footer-title {
        margin-bottom: 20px;
        font-size: 28px;
        text-transform: uppercase;
        color: #fff;
        font-weight: 600;
        @include breakpoint(md){
            font-size: 25px;
        }
        @include breakpoint(xs){
            font-size: 22px;
        }
    }
    .footer-logo {
        margin-bottom: 30px;
        display: block;
    }
    
    .footer-form {
        background: #fff;
        border-color: transparent;
        height: 42px;
        border-radius: 999px !important;
        font-size: 16px;
        color: var(--body-font-color);
        @include afterparent();
        width: 100%;
        button {
            width: 36px;
            border: none;
            background-color: var(--primary_color);
            color: #fff;
            border-radius: 50% !important;
            height: 36px;
            line-height: 36px;
            font-size: 20px;
            padding: 0px;
            position: absolute;
            right: 3px;
            top: 50%;
            transform: translateY(-50%);
        }
        .form-control{
            height: 100%;
            padding-left: 20px;
            border: none;
            border-radius: 999px;
            font-size: 16px;
            &:focus {
                border-color: var(--primary_color);
                box-shadow: none;
                outline: none;
            }
            &::placeholder {
                color: var(--body-font-color);
                font-weight: 300;
                font-size: 14px;
            }
        }
    }
    ul.rt-usefulllinks {
        margin: 0;
        padding: 0;
        list-style: none;
        li {
            display: inline-block;
            width: 48%;
            margin-bottom: 20px;
            position: relative;
            &:after {
                position: absolute;
                left: -10px;
                top: 50%;
                transform: translateY(-50%);
                height: 4px;
                width: 4px;
                border-radius: 50%;
                background-color: var(--primary_color);
                content: '';
                transition: all 0.24s ease-in-out;
            }
            &:last-child {
                margin-bottom: 0px;
            }
            a {
                display: block;
                color: #fff;
                transition: all 0.24s ease-in-out;
                &:hover {
                    color: var(--primary_color);
                    padding-left: 5px;
                }
            }
            &:hover {
                &:after {
                    background: var(--secondary_color);
                    animation: zoomIn 0.24s ease-in-out;
                }
            }
        }
    }
    ul.rt-usefulllinks2 {
        margin: 0;
        padding: 0;
        list-style: none;
        li {
            display: block;
            padding-bottom: 10px;
            padding-left: 20px;
            @include afterparent();
            &::after {
                position: absolute;
                left: 0;
                top: 0px;
                content: '\eac7';
                font-family: 'icofont';
                color: #fff;
            }
            &:last-child {
                margin-bottom: 0px;
            }
            a {
                display: block;
                color: #fff;
                @include rt-trs(0.34s);
            }
            &:hover {
                a {
                    color: var(--primary_color);
                    padding-left: 8px;
                }
                &::after{
                    color: var(--primary_color);
                }
            }
        }
    }
}

.footer-bottom {
    .copy-text {
        color: #c7c9fc;
        font-size: 16px;
        @include breakpoint(md){
            margin-bottom: 15px;
        }
        a {
            color: #ff5500;
        }
    }
    .rt-list {
        li {
            display: inline-block;
            margin-right: 6px;
            &:last-child {
                margin-right: 0px;
            }
            a {
                display: block;
                img {
                    height: 25px;
                }
            }
        }
    }
}




  