.rt-site-header {
    position: relative;
    @include rt-trs(0.24s);

    .login-btn {
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        width: 1140px;
        text-align: right;
        top: 0;

        a {
            img {
                border-bottom-left-radius: 30px;
                border-bottom-right-radius: 30px;
            }
        }
        @include breakpoint(lg) {
            width: 960px;
        }
        @include breakpoint(md) {
            width: 100%;
            display: none;
        }
    }
   
    &.has_login_icon {
        .top-header,
        .main-header {
            width: calc(1140px - 95px);
            left: calc(50% - 45px);
            transform: translateX(-50%);
            position: relative;
            z-index: 99;
            @include breakpoint(lg) {
                width: calc(960px - 95px);
            }
            @include breakpoint(md) {
                width: 100%;
                left: 0;
                transform: none;
            }
        }
    }

    .main-header {
        background-color: transparent;
        .sticky_logo{
            display: none;
        }

        .navbar {
            > .container {
                padding-left: 0px;
                padding-right: 0px;
                position: relative;
                @include breakpoint(xs) {
                    padding-left: 15px;
                    padding-right: 15px;
                }
            }
        }
    }

    .top-header {
        transition: all 0.3s ease-in-out;

        .container {
            border-bottom: 1px solid rgba(255, 255, 255, 0.2);
            padding-left: 0px;
            padding-right: 0px;
            padding-top: 6px;
            padding-bottom: 6px;
            animation: fade-in-top 0.6s cubic-bezier(0.39, 0.575, 0.565, 1) both;
            @include breakpoint(xs){
                padding-left: 15px;
                padding-right: 15px;
            }
        }
    }

    .brand-logo {
        max-width: 150px;
        transition: all 0.3s ease-in-out;

        @include breakpoint(xs) {
            max-width: 140px;
        }
    }

    .navbar {
        padding: 0;
        transition: 0.3s ease-in-out;

        @include breakpoint(md) {
            padding: 30px 0;
        }
    }

    &.rt-fixed-top {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        z-index: 9;
    }

    .main-menu,
    .rt-nav-tolls {
        transition: all 0.3s ease-in-out;
    }

    &.rt-sticky {
        &.rt-sticky-active {
            .top-header {
                display: none;
            }

            position: fixed;
            width: 100%;
            z-index: 99;
            background-color: var(--secondary_color);
            box-shadow: 0px 2px 3px 0px rgba(#000, 0.1);

            .main-menu > ul > li > a {
                line-height: 65px;

                @include breakpoint(md) {
                    line-height: 1.9;
                }
            }
            .main-header{
                .sticky_logo{
                    display: block;
                }
                .brand-logo{
                    display: none;
                }
            }
        }
    }
}

.tp-l-list {
    @include breakpoint(xs) {
        text-align: center;
    }
    li {
        display: inline-block;
        @include afterparent();
        font-size: 14px;
        margin-right: 10px;

        &.help-icon {
            padding-right: 54px;

            &::after {
                position: absolute;
                right: 20px;
                top: 50%;
                transform: translateY(-50%);
                height: 15px;
                width: 1px;
                content: "";
                background-color: rgba(230, 236, 255, 0.4);
            }

            img {
                margin-right: 10px;
            }
        }

        &:last-child {
            margin-right: 0px;
        }

        color: #fff;

        a {
            display: block;
            color: #fff;
        }

        &:hover {
            color: var(--primary_color);

            a {
                color: var(--primary_color);
            }
        }
    }
}

.tp-r-list {
    display: flex;
    @extend .rt-list;
    justify-content: flex-end;
    @include breakpoint(xs) {
        justify-content: center;
    }

    li {
        @include afterparent();
        font-size: 14px;
        margin-right: 84px;
        @include breakpoint(md) {
            margin-right: 25px;
        }

        &:last-child {
            margin-right: 0px;
        }

        .select-arrwo {
            position: absolute;
            right: 0px;
        }

        .select2-container--default .select2-selection--single .select2-selection__rendered {
            font-size: 13px;
            color: #fff;
            font-weight: 400;
        }

        color: #fff;

        a {
            color: #fff;

            .check-icon {
                margin-right: 8px;
            }
        }
    }
}


.home-three{
    .tp-l-list li,
    .tp-l-list li a {
        color: var(--body-font-color);
    }
    .tp-l-list li.help-icon{
        &::after{
            background-color:rgba( #38496d, 0.4);
        }
    }
    .tp-l-list{
        @include breakpoint(xs){
            li{
                a{
                    color: white;
                }
                &.help-icon{
                    img{
                        display: none;
                    }
                    &::before{
                        position: absolute;
                        left: -20px;
                        top: -2px;
                        content: '\ef77';
                        font-family: "icofont";
                        font-size: 16px;
                        color: white;
                    }
                    &::after{
                        background-color:rgba( #fff, 0.4);
                    }
                }
            }
        }
    }

    @include breakpoint(xs){
        .brand-logo{
            display: none;
        }
        .sticky_logo{
            display: inline-block !important;
            max-width: 130px;
        }
    }
}