.price-carosle-wrapper {
    max-width: 1130px;
    margin-left: auto;
    padding-right: 30px;
    @include breakpoint(lg) {
        max-width: 960px;
    }
    @include breakpoint(md) {
        max-width: 720px;
    }
    @include breakpoint(xs) {
        padding-left: 30px;
    }
}

.rt-single-icon-box {
    &.price-box {
        background-image: url(../images/all-img/hover-1.png);
        background-color: #f7f7fb;
        @include rt-trs(0.4s);
        border-radius: 24px;
        background-repeat: no-repeat;
        padding-top: 60px;
        padding-bottom: 100px;
        box-shadow: 0px 10px 10px 0px rgba(132, 141, 156, 0.3);
        background-position: 280px;
        background-size: 100% 100%;
        min-height: 520px;
        transform: translateY(0px);
        @include breakpoint(xs) {
            min-height: auto;
            padding-bottom: 60px;
        }
        .icon-thumb {
            margin-bottom: 35px;
        }
        .iconbox-content {
            h4 {
                font-size: 24px;
                font-weight: 700;
                line-height: 34px;
                color: #33406a;
                margin: 0 0 15px;
                text-transform: uppercase;
            }
            h5 {
                font-size: 40px;
                font-weight: 600;
                color: #224fe7;
                text-shadow: 1.753px 3.595px 2px rgba(#224fe7, 0.22);
                @include breakpoint(xs) {
                    font-size: 32px;
                }
            }
            .hover-link {
                position: absolute;
                bottom: 16px;
                right: 80px;
                text-transform: capitalize;
                @include rt-trs(0.3s);
                @include hidden();
                color: #fff;
                span {
                    display: inline-block;
                    padding-left: 8px;
                    position: relative;
                    top: 3px;
                    @include breakpoint(xs) {
                        top: 0;
                        img {
                            display: none;
                        }
                    }
                }
            }
        }
        &:hover {
            background-position: 0px;
            box-shadow: 0px 10px 20px 0px rgba(132, 141, 156, 0.3);
            transform: translateY(-5px);
            .hover-link {
                @include vis();
                right: 18px;
                transition-delay: 0.1s;
            }
        }
    }
}

.rt-single-icon-box {
    &.price-box2 {
        background-color: #fff;
        @include rt-trs(0.4s);
        border-radius: 10px;
        background-repeat: no-repeat;
        padding-top: 50px;
        padding-bottom: 50px;
        box-shadow: 1.753px -3.595px 10px 0px rgba(132, 141, 156, 0.3);
        min-height: 445px;
        .icon-thumb {
            margin-bottom: 35px;
        }
        .iconbox-content {
            h4 {
                font-size: 24px;
                font-weight: 700;
                line-height: 34px;
                color: #33406a;
                margin: 0 0 15px;
                text-transform: uppercase;
            }
            h5 {
                font-size: 40px;
                font-weight: 600;
                color: #224fe7;
                text-shadow: 1.753px 3.595px 2px rgba(#224fe7, 0.22);
            }
            .hover-link-mbl {
                position: absolute;
                bottom: 16px;
                right: 80px;
                text-transform: capitalize;
                @include rt-trs(0.3s);
                @include hidden();
                color: #fff;
                span {
                    display: inline-block;
                    padding-left: 8px;
                    position: relative;
                    top: 3px;
                }
            }
        }
    }
}

.price-list-parent {
    @extend .rt-list;
    display: flex;
    flex-wrap: wrap;
    li {
        @include flex-width(50%);
        margin-bottom: 30px;
        height: 531px;
        @include afterparent();
        &:nth-child(2n) {
            top: 174px;
        }
        @include breakpoint(xs) {
            @include flex-width(100%);
            &:nth-child(2n) {
                top: unset;
            }
            height: auto;
        }
        .hover-link {
            position: absolute;
            left: 25px;
            top: 0px;
            background: var(--gradient-primary);
            height: 479px;
            width: calc(100% - 25px);
            border-radius: 10px;
            display: flex;
            flex-direction: column;
            justify-content: end;
            align-items: center;
            padding-bottom: 24px;
            @include rt-trs(0.34s);
            box-shadow: 0px 10px 10px 0px rgba(132, 141, 156, 0.3);
            @include hidden();
            @include breakpoint(xs) {
                display: none;
            }
            a {
                color: white;
                display: inline-block;

                span {
                    margin-left: 10px;
                    display: inline-block;
                }
            }
        }
        &.active {
            .hover-link {
                top: 50px;
                @include vis();
                a {
                    animation: fade-in-left 0.6s cubic-bezier(0.39, 0.575, 0.565, 1) both;
                    animation-delay: 0.1s;
                }
            }
        }
    }
}

.price2-area {
    background-repeat: no-repeat;
    background-size: 100% 100%;
    background-position: 0 300px;
    
    @include breakpoint(lg){
        background-size: cover;
        background-position: center center;
    }
    @include breakpoint(md){
        //background-image: none !important;
    }
    .price-bgi{
        position: absolute;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
        @include breakpoint(xl){
            img{
                height: 750px;
            }
        }
        @include breakpoint(lg){
            img{
                height: 450px;
            }
        }
        @include breakpoint(md){
            display: none;
        }
    }
}

.active-price {
    .slick-slide {
        margin: 0 15px;
    }
    .slick-list {
        margin: 0 -15px;
    }
    .single-item {
        margin-top: 50px;
        margin-bottom: 50px;
    }
}

