.all-price-boxes{
    .rt-single-icon-box{
        &.price3-box{
            &:nth-child(2n){
                .icon-thumb{
                    order: 0;
                    margin-right: 50px;
                    @include breakpoint(md){
                        margin-right: 0px;
                        margin-bottom: 30px;
                    }
                }
                .iconbox-content{
                    order: 1;
                }
            }
            &:nth-child(2n + 1){
                .icon-thumb{
                    order: 1;
                    margin-left: 50px;
                    @include breakpoint(md){
                        margin-left: 0px;
                        margin-top: 30px;
                    }
                }
                .iconbox-content{
                    order: 0;
                }
            }
        }
    }
}