// hunt domain section
.hunt-domian {
    background-repeat: no-repeat;
    background-position: center center;
}

.domain-content {
    .tbanavs {
        .nav-item {
            margin-right: 25px;
            &:last-child {
                margin-right: 0px;
            }
            @include breakpoint(xs) {
                margin-right: 0px;
                margin-bottom: 10px;
                &:last-child {
                    margin-bottom: 0px;
                }
            }
            .nav-link {
                background-color: rgba(255, 255, 255, 0.678);
                box-shadow: 0px 10px 10px 0px rgba(132, 141, 156, 0.3);
                color: #38496d;
                border-radius: 999px;
                padding: 10px 55px;
                font-size: 16px;
                text-transform: uppercase;
                &.active {
                    background: var(--gradient-primary);
                    color: #fff;
                }
            }
        }
    }
}

.domian-serach-box {
    max-width: 923px;
    margin-left: auto;
    margin-right: auto;
    .domain-searh-form {
        display: flex;
        justify-content: space-between;
        @include breakpoint(xs) {
            flex-direction: column;
            justify-content: center;
            align-items: center;
        }
    }
}
.hunt-bg {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    @include breakpoint(lg){
        display: none;
    }
}
.hunt-domian2{
    @include breakpoint(lg){
        padding-top: 80px;
        padding-bottom: 80px;
        background-color: var(--secondary_color);
    }
    @include breakpoint(md){
        padding-top: 60px;
        padding-bottom: 40px;
    }
}
.domianinput-fileds {
    background-color: rgb(255, 255, 255);
    box-shadow: 0px 5px 30px 0px rgba(132, 141, 156, 0.3);
    height: 60px;
    max-width: 636px;
    @include breakpoint(md) {
        max-width: 450px;
    }
    display: flex;
    flex: 1;
    justify-content: space-between;
    align-items: center;
    border-radius: 999px;
    @include afterparent();
    @include breakpoint(xs) {
        flex: unset;
        margin-bottom: 10px;
    }
    .form-bdage {
        position: absolute;
        left: 30px;
        top: 50%;
        transform: translateY(-50%);
        z-index: 2;
        @include breakpoint(xs) {
            left: 10px;
            img {
                height: 15px;
            }
        }
    }
    &:after {
        position: absolute;
        right: 133px;
        top: 50%;
        transform: translateY(-50%);
        height: 25px;
        width: 1px;
        background-color: rgba(#5d5c64, 0.5);
        content: "";
        @include breakpoint(xs) {
            right: 80px;
        }
    }
    input[type="text"],
    input[type="email"] {
        flex: calc(100% - 150px);
        max-width: calc(100% - 150px);
        border-radius: 999px;
        height: 100%;
        border: none;
        padding-left: 66px;
        @include afterparent();
        @include breakpoint(xs) {
            flex: calc(100% - 85px);
            max-width: calc(100% - 85px);
            padding-left: 30px;
        }
        &::placeholder {
            font-size: 16px;
            color: #939393;
            font-weight: 400;
        }
    }
    .select-optin {
        flex: 0 0 95px;
        max-width: 95px;
        @include breakpoint(xs) {
            flex: 0 0 80px;
            max-width: 80px;
        }
    }

    .select2-container--default .select2-selection--single .select2-selection__rendered {
        font-size: 16px;
        color: #2a2a2a;
        font-weight: 600;
    }
    .select2-container--default .select2-selection--single .select2-selection__arrow {
        display: block;
        &:after {
            position: absolute;
            right: 25px;
            font-size: 15px;
            top: 50%;
            transform: translateY(-50%);
            content: "\f113";
            font-family: "Flaticon";
            color: #939393;
            @include breakpoint(xs){
                right: 10px;
            }
        }
    }
    .select2-container--default .select2-selection--single .select2-selection__arrow b {
        display: none;
    }
}

.hosting-badge {
    background-color: #fff;
    box-shadow: 1.753px 3.595px 20px 0px rgba(34, 79, 231, 0.1);
    height: 176px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    border-radius: 20px;
    @include rt-trs(0.24s);
    background-image: url(../images/all-img/hi.png);
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center center;
    span {
        &.domain-name {
            font-size: 36px;
            font-weight: 600;
            line-height: 46px;
        }
        &.domain-yr {
            color: $primary-color;
            font-size: 20px;
            font-weight: 600;
        }
    }
    @include breakpoint(xs){
        height: 130px;
        span{
            &.domain-name{
                font-size: 25px;
                line-height: 35px;
            }
            &.domain-yr{
                font-size: 18px;
            }
        }
    }
    &:hover {
        box-shadow: 2.816px 17.778px 13px 0px rgba(44, 71, 163, 0.21);
        transform: translateY(-3px);
        background-image: url(../images/all-img/hi2.png);
    }
}


.hosting-badge2{
    border-radius: 5px;
    background-color: rgb(255, 255, 255);
    box-shadow: 0px 3px 10px 0px rgba(24, 13, 91, 0.1);
    height: 83px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    @include rt-trs(0.24s);
    span {
       // @include rt-trs(0.24s);
        &.domain-name {
            font-size: 24px;
            font-weight: 600;
            line-height: 34px;
            color: var(--body-font-color);
        
        }
        &.domain-yr {
            color: $primary-color;
            font-size: 18px;
            &.clr-1{ color:#e6457b }
            &.clr-2{color: #4bcc88}
            &.clr-3{color: #215bee}
        }
    }
    &:hover{
        background: linear-gradient( 105deg, rgb(100,87,198) 0%, rgb(48,210,190) 0%, rgb(48,210,190) 0%, rgb(44,107,241) 0%, rgb(26,89,231) 100%);
        span{
            &.domain-name{
                color: #f87c15;
            }
            &.domain-yr{
                color: #e3e6ff !important;
            }
        }
          
    }
      
}

