.rt-breadcump {
  margin-bottom: 40px;
  //background-color: var(--secondary_color);
  position: relative;
  color: #000000;

  a,
  p {
    color: #fff;
    font-size: 15px;
    position: relative;
  }

  @include breakpoint(xs) {
    margin-bottom: 70px;
  }
.divider{
  color: white;
  display: inline-block;
  margin-right: 5px;
}
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    font-size: 60px;
    color: #fff;
    font-weight: 700;
    text-transform: capitalize;
    text-shadow: 0px 5px 10px rgba(15, 1, 85, 0.2);

    @include breakpoint(xs) {
      font-size: 32px;
    }
  }
  .rt-breadcump-height {
    height: 700px;

    @include breakpoint(xs) {
      height: 550px;
    }
  }
.breadcrumbs{
  color: white;
  a{
    display: inline-block;
    margin-right: 5px;
  }
}

  .rt-page-bg {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
  }
}
