.testibg {
    padding-top: 118px;
    padding-bottom: 118px;
    padding-left: 30px;
    @include breakpoint(md){
        padding-top: 60px;
        padding-bottom: 80px;
    }
}

.testi_active {
    .slick-dots li button:before {
        display: none !important;
    }

    .slick-dots {
        margin: 0;
        padding: 0;
        list-style: none;
        bottom: -60px;
        text-align: left;
        @include breakpoint(xs){
            text-align: center;
        }

        li {
            @include afterparent();
            width: 16px;
            display: inline-block;
            height: 16px;
            margin: 0 5px;

            button {
                border-radius: 50%;
                height: 10px;
                width: 10px;
                background-color: #a5b2eb;
                
            }
        }
    }

    .slick-dots {
        li {
            button {
                overflow: hidden;
                background-color:  #a5b2eb;
                transition: all 0.3s ease;
            }

            &.slick-active {
                button {
                    height: 16px;
                    width: 16px;
                   background-color: var(--primary_color);
                }
            }
        }
    }

    .single-item {
        h6 {
            font-size: 24px;
            font-weight: 600;
            margin-bottom: 33px;
            line-height: 36px;
            @include breakpoint(xs){
                font-size: 20px;
                line-height: 30px;
            }

            span {
                display: inline-block;
                margin-right: 10px;
            }
        }

        .rt-single-icon-box {
            @include breakpoint(xs){
                flex-direction: column;
            align-items: center;
            justify-content: center;
            text-align: center;
            }
            .icon-thumb {
                margin-right: 22px;
                @include breakpoint(xs){
                    margin-right: 0px;
                    margin-bottom: 20px;
                }
            }

            .iconbox-content {


                h4 {
                    font-size: 18px;
                    font-weight: 600;
                    margin: 0px 0 0;

                    span {
                        font-size: 16px;
                        margin: 0 0 10px;
                    }
                }
            }
        }
    }
}

.bg-e{
    position: absolute;
    left: -100px;
    top: 0;
    @include breakpoint(xl){
        left: 0px;
        max-width: 380px;
    }

    @include breakpoint(lg){
        max-width: 320px;
    }
    @include breakpoint(md){
        position: static;
        margin-bottom: 30px;
    }
}