// global
.rt-single-icon-box {
    display: flex;
    @include afterparent();
    align-items: start;
    @include rt-trs(0.24s);
    .icon-thumb {
        flex: 0 0 auto;
        //height: 58px
        //margin-right: 30px
        img {
            @include rt-trs(0.24s);
        }
    }
    &.icon-center {
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    &.rt-xs-center {
        @include breakpoint(xs) {
            flex-direction: column;
        }
    }
    .iconbox-content {
        width: 100%;
        h4 {
            a {
                color: var(--body-font-color);
            }
            @include breakpoint(xs) {
                font-size: 20px !important;
                line-height: 30px !important;
            }
        }
    }
    &.plain-list-1 {
        background-color: transparent;
        border-bottom: 1px solid rgb(240, 240, 240);
        &:last-child {
            border: none;
        }
    }
    &.rt-align-center {
        align-items: center;
    }
    &.bg-transparent {
        background-color: transparent;
    }
    &.shdoaw-style {
        box-shadow: 0px 18px 65px 0px rgba(153, 153, 159, 0.2);
        background-color: #fff;
    }
    &:hover {
        &.shdoaw-style {
            box-shadow: 0px 18px 45px 0px rgba(153, 153, 159, 0.2);
            transform: translateY(-10px);
        }
    }
}

// service box
.active-price {
    .slick-next,
    .slick-prev {
        position: absolute;
        left: 50%;
        text-align: center;
        color: #000;
        z-index: 66;
        cursor: pointer;
        background-color: #364b86;
        box-shadow: 0 10px 10px 0 rgba(132, 141, 156, 0.1);
        border-radius: 50%;
        @include hw(65px);
        bottom: -45px;
        top: unset;
        transform: translateY(0) translateX(-50%);
        opacity: 1;
        @include rt-trs(0.2s);
        background-size: auto;
        background-repeat: no-repeat;
        background-position: center center;
        z-index: 1;
        @include breakpoint(xs) {
            @include hw(40px);
            background-size: 20px;
            bottom: 20px;
            left: calc(50% - 30px);
        }
        &::before {
            display: none;
        }
        &:hover {
            background-color: #ec5e50;
            &::before {
                opacity: 1;
                color: #fff;
            }
        }
    }
    .slick-prev {
        background-image: url("../images/all-img/ar-wl.png");
    }
    .slick-next {
        background-image: url("../images/all-img/ar-wr.png");
        margin-left: 80px;
        @include breakpoint(xs) {
            margin-left: 56px;
        }
    }
}
.rt-single-icon-box {
    &.services-box {
        box-shadow: 0px 10px 10px 0px rgba(132, 141, 156, 0.3);
        border-radius: 30px;
        padding-top: 34px;
        padding-left: 32px;
        padding-right: 25px;
        padding-bottom: 40px;
        background-color: #fff;
        @include breakpoint(lg) {
            min-height: 380px;
        }
        .icon-thumb {
            margin-bottom: 30px;
        }
        .iconbox-content {
            h4 {
                font-size: 24px;
                line-height: 34px;
                font-weight: 700;
                margin-bottom: 15px;
                @include breakpoint(xs) {
                    font-size: 20px;
                    line-height: 30px;
                }
            }
            p {
                margin: 0 0 20px;
            }
            a {
                font-size: 18px;
                font-weight: 600;
                color: var(--body-font-color);
                @include breakpoint(xs) {
                    font-size: 16px;
                    span {
                        img {
                            height: 15px;
                        }
                    }
                }
                span {
                    margin-left: 10px;
                    font-size: 20px;
                }
            }
        }
    }
}

// feature box
.rt-single-icon-box {
    &.feature-box {
        border: 1px solid rgba(57, 91, 225, 0.5);
        border-radius: 30px;
        padding-top: 40px;
        padding-bottom: 28px;
        padding-left: 40px;
        padding-right: 40px;
        background-color: #fff;
        .icon-thumb {
            margin-bottom: 30px;
        }
        .iconbox-content {
            h4 {
                font-size: 24px;
                line-height: 38px;
                font-weight: 700;
                margin-bottom: 10px;
            }
        }
        @include breakpoint(xs) {
            padding-left: 25px;
            padding-right: 25px;
        }
        &:hover {
            box-shadow: -1.22px 13.947px 20px 0px rgba(2, 96, 255, 0.14);
            border-color: transparent;
        }
    }
}

//icon list css
.rt-single-icon-box {
    &.list-item-1 {
        background-color: rgba(255, 255, 255, 0.051);
        border: 1px solid rgba(#fff, 0.4);
        border-radius: 20px;
        padding-top: 35px;
        padding-left: 30px;
        padding-right: 30px;
        padding-bottom: 30px;

        .icon-thumb {
            margin-right: 30px;
        }
        .iconbox-content {
            h4 {
                font-size: 24px;
                line-height: 38px;
                font-weight: 700;
                margin-bottom: 10px;
            }
        }
        @include breakpoint(xs) {
            flex-direction: column;
            justify-content: center;
            align-items: center;
            text-align: center;
            padding-left: 25px;
            padding-right: 25px;
            .icon-thumb {
                margin-right: 0px;
                margin-bottom: 30px;
            }
        }
    }
}

.rt-single-icon-box {
    &.list-item-2 {
        background-color: #fff;
        border: 1px solid rgb(231, 232, 242);
        box-shadow: 0px 5px 20px 0px rgba(156, 165, 181, 0);
        padding: 30px;
        &::after {
            position: absolute;
            left: 0;
            top: 0px;
            height: 100%;
            width: 6px;
            background-color: rgb(231, 232, 242);
            content: "";
            z-index: 2;
            @include rt-trs(0.24s);
        }
        &::before {
            position: absolute;
            left: 0;
            bottom: 0px;
            height: 0%;
            width: 6px;
            background-color: #224fe7;
            content: "";
            z-index: 3;
            @include rt-trs(0.24s);
        }
        .icon-thumb {
            margin-right: 30px;
        }
        .iconbox-content {
            h4 {
                font-size: 24px;
                line-height: 38px;
                font-weight: 700;
                margin-bottom: 10px;
            }
        }
        &:hover {
            box-shadow: 0px 5px 20px 0px rgba(156, 165, 181, 0.5);
            border-color: transparent;
            &:after {
                height: 0;
            }
            &::before {
                height: 100%;
                transition-delay: 0.2s;
            }
        }
    }
}

// counter box
.counter-area {
    @include afterparent();
    margin-top: -80px;
}

.rt-single-icon-box {
    &.single-counter {
        .icon-thumb {
            margin-right: 30px;
        }
        .iconbox-content {
            h4 {
                font-size: 60px;
                font-weight: 700;
                line-height: 70px;
            }
        }
    }
    &.single-counter2 {
        @extend .single-counter;
        .iconbox-content {
            h4 {
                font-size: 40px;
                line-height: 50px;
                font-weight: 600;
            }
            p {
                color: #526288;
                font-weight: 300;
                font-size: 22px;
            }
        }
    }
}

// price list item
.install-list-item {
    @include afterparent();
    height: 300px;
    @include breakpoint(md) {
        display: flex;
        flex-wrap: wrap;
        height: auto;
        margin-top: 30px;
    }
    li {
        position: absolute;
        img {
            max-width: 90px;
            @include breakpoint(md) {
                max-width: 100%;
            }
        }
        @include breakpoint(md) {
            position: static;
            @include flex-width(20%);
        }
        @include breakpoint(xs) {
            @include flex-width(33.33%);
        }

        &:hover {
            img {
                animation: pulse 400ms ease-in-out;
            }
        }
        &:nth-child(1) {
            left: 0;
            top: 0;
        }
        &:nth-child(2) {
            left: 9%;
            top: 42px;
        }
        &:nth-child(3) {
            top: 85px;
            left: 18%;
            @include breakpoint(md) {
                left: 22%;
                top: 89px;
            }
        }
        &:nth-child(4) {
            top: 42px;
            left: 27%;
        }
        &:nth-child(5) {
            top: 85px;
            left: 36%;
        }
        &:nth-child(6) {
            top: 42px;
            left: 45%;
        }
        &:nth-child(7) {
            top: 85px;
            left: 54%;
        }
        &:nth-child(8) {
            top: 0px;
            right: 0px;
        }
        &:nth-child(9) {
            top: 42px;
            right: 9%;
        }
        &:nth-child(10) {
            top: 85px;
            right: 18%;
        }
        &:nth-child(11) {
            top: 42px;
            right: 27%;
        }
        &:nth-child(12) {
            top: 100px;
            right: 0;
        }
        &:nth-child(13) {
            top: 100px;
            left: 0;
        }
        &:nth-child(14) {
            top: 145px;
            left: 9%;
        }
        &:nth-child(15) {
            top: 145px;
            right: 9%;
        }
        &:nth-child(16) {
            top: 145px;
            left: 27%;
        }
        &:nth-child(17) {
            top: 145px;
            left: 45%;
        }
        &:nth-child(18) {
            top: 145px;
            right: 27%;
        }
    }
}

//help-box"
.rt-single-icon-box {
    &.help-box {
        padding: 30px;
        height: 145px;
        box-shadow: 0px 5px 20px 0px rgba(183, 184, 185, 0.2);
        background-color: #fff;
        .icon-thumb {
            margin-right: 30px;
        }
        @include breakpoint(lg) {
            height: auto;
        }
        .iconbox-content {
            h4 {
                font-size: 28px;
                font-weight: 600;
                margin: 0 0 10px;
            }
        }
    }
}

// feature list
.feature-parent-item {
    li {
        @include afterparent();
        @include breakpoint(xs){
            position: static;
        }
        &:nth-child(2n){
            top: 74px;
        }
    }
}

.rt-single-icon-box {
    &.feature-list-item {
        padding-top: 55px;
        overflow: hidden;
        padding-bottom: 50px;
        padding-left: 20px;
        padding-right: 20px;
        border-radius: 30px;
        background-color: rgb(255, 255, 255);
        box-shadow: 0px 15px 27px 0px rgba(62, 97, 157, 0.3);
        height: 334px;
        .icon-thumb{
            margin-bottom: 30px;
        }
        .iconbox-content{
            @include rt-trs(0.24s);
            h4{
                font-size: 24px;
                line-height: 28px;
                font-weight: 600;
                margin: 0 0 10px;

            }
            h4,p{
                @include rt-trs(0.24s);
            }
            .hover_link{
                display: none;
            }
        }
        &::after{
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            background: linear-gradient( 105deg, rgb(100,87,198) 0%, rgb(48,210,190) 0%, rgb(48,210,190) 0%, rgb(44,107,241) 0%, rgb(26,89,231) 100%);
            content: "";
            border-radius: 30px;
            z-index: -1;
            @include rt-trs(0.24s);
            @include hidden();
        }
        &:hover{
            .icon-thumb{
                
                animation: fade-out-top 0.7s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
                //height: 0;
            }
            .iconbox-content{
                padding-bottom: 67px;
                h4, p{
                    color: white;
                }
            }
            .hover_link{
                display: block;
                animation: zoomIn 0.6s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
            }
            &::after{
                @include vis();
            }
        }
    }
}

