.rt-nav-tolls {
    margin-left: 30px;
    position: relative;
    @include breakpoint(xs){
        display: none;
    }
    ul{
        margin: 0;
        padding: 0;
        list-style: none;
        li{
            display: inline-block;
            margin-right: 30px;
            @include afterparent();
            line-height: 74px;
            @include breakpoint(lg){
                margin-right: 15px;
            }
            @include breakpoint(md){
                margin-right: 7px;
                line-height: normal;
            }

            &:last-child{
                margin-right: 0px;
            }
            a{
                display: block;
               
                @include rt-trs(0.24s);
                &:focus{
                    outline: none;
                }
                i{
                    font-size: 20px;
                    @include breakpoint(lg){
                        font-size: 16px;
                    }
                }
               
            }
            &:hover{
                > a{
                    color: $primary-color
                }
            }
        }
    }
    
}

.white-menu {
    .rt-nav-tolls {
        span {
            a {
                color: #fff;
            }
        }
    }
}

.catagory-drops {
    @include breakpoint(lg){
        display: none;
    }
    a {
        color: rgb(17, 20, 32);
        font-size: 18px;
        &.dropdown-toggle::before {
            font-family: 'icofont';
            position: absolute;
            top: 50%;
            right: -20px;
            content: '\ea99';
            transform: translateY(-50%);
        }
        &.dropdown-toggle::after{
            display: none;
        }
    }
}

// include nav search 
@import "nav-search";
@import "offcanvas";
@import "nav-cart";