html {
    height: 100%;
}

body {
    height: 100%;
    line-height: var(--line-height);
    font-size: var(--body-font-size);
    color: var(--body-font-color);
    font-family: "Work Sans", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    vertical-align: baseline;
    text-rendering: optimizeLegibility;
    font-weight: 400;
    overflow-x: hidden;
    counter-reset: my-sec-counter;
}

a {
    transition: all 0.24s ease-in-out;
    &:hover {
        text-decoration: none;
        color: var(--primary_color);
    }
    &.link-text {
        color: var(--body-font-color);
    }
    &.link-text2 {
        color: var(--body-font-color);
        text-decoration: underline;
    }
}

img {
    max-width: 100%;
}

.owl-item img {
    width: auto !important;
    margin-left: auto;
    margin-right: auto;
}

.rt-list {
    margin: 0;
    padding: 0;
    list-style: none;
}

.mouse-cursor {
    cursor: pointer;
}

button.mfp-close {
    background: #fff;
    color: var(--primary_color) !important;
    opacity: 1;
    font-size: 20px;
    height: 35px;
    width: 35px !important;
    border-radius: 50%;
    text-align: center !important;
    line-height: 35px !important;
    padding: 0 !important;
    position: absolute;
    top: -52px !important;
}

img.mfp-img {
    padding: 0px !important;
    border: 6px solid #fff;
    box-shadow: 0px 25px 20px 0 rgba(136, 136, 136, 0.35) !important;
}

.mfp-counter {
    right: 15px;
    color: #fff;
    font-size: 14px;
    background: var(--secondary_color);
    padding: 3px 16px;
    border-radius: 3px;
    font-weight: 700;
}

.mfp-content {
    border: 6px solid #fff;
    box-shadow: 0px 25px 20px 0 rgba(136, 136, 136, 0.35) !important;
}

.smallgap.row > [class*="col-"] {
    padding-left: 3px !important;
    padding-right: 3px !important;
}

#scrollUp {
    position: fixed;
    right: 30px;
    bottom: 30px;
    height: 50px;
    width: 50px;
    line-height: 50px;
    text-align: center;
    background: var(--gradient-primary);
    color: #fff;
    box-shadow: 0px 10px 10px 0px rgba(132, 141, 156, 0.3);
    font-size: 20px;
    border-radius: 50%;
    @include breakpoint(xs){
        @include hw(40px);
    }
}

.review {
    color: #ffab53;
}

.bg-fixed {
    background-attachment: fixed;
}

blockquote {
    background: #f3f3f3;
    font-size: 20px;
    line-height: 30px;
    font-weight: 400;
    color: var(--body-font-color);
    padding-left: 37px;
    padding-top: 40px;
    padding-right: 30px;
    padding-bottom: 30px;
    position: relative;
    border-left: 4px solid #fc4a1a;
    h5 {
        margin: 20px 0 0;
        color: #888888;
        font-size: 15px;
    }
}

.slick-slide {
    outline: none;
}

// section padding
// overlay
.rt-inner-overlay,
.rt-overlay-content {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    &.white-overlay {
        background-color: rgba(255, 255, 255, 0.8);
    }
    &.white-overlay2 {
        background-color: rgba(255, 255, 255, 0.502);
    }
}

.rt-overlay-content {
    &.white-overlay2 {
        background-color: rgba(255, 255, 255, 0.502);
    }
    z-index: auto;
}

.rtbgprefix-cover {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
}

.rtbgprefix-contain {
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center center;
}

.rtbgprefix-full {
    background-size: 100% 100%;
    background-repeat: no-repeat;
    background-position: center center;
}

.position-parent {
    position: relative;
    z-index: 1;
    .map-img{
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        width: 48%;
    }
}
.video-middle {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}
.step-area {
    margin-bottom: 260px;
    @include breakpoint(md) {
        margin-bottom: 50px;
    }
}
.step2-area {
    @include breakpoint(md) {
        background-image: none !important;
        background-color: var(--secondary_color);
    }
}
.feature2-area {
    @include breakpoint(xs) {
        background-image: none !important;
        background-color: var(--secondary_color);
    }
}
.services-area {
    @include breakpoint(xs) {
        background-image: none !important;
        background-color: var(--secondary_color);
        padding-bottom: 70px;
    }
}
#{$all-buttons}{
    border: none
}
#{$all-buttons-active},
#{$all-buttons-focus},
#{$all-buttons-hover},
#{$all-text-inputs-focus},
#{$all-text-inputs-active},
#{$all-text-inputs-hover},
#{$all-text-inputs-invalid} {
    outline: none;
    box-shadow: none;
}

@for $i from 0 through 50 {
    .rt-rounded-#{$i} {
        border-radius: $i + px;
    }
}

@for $i from 0 through 10 {
    .rt-mb-#{$i* 10} {
        margin-bottom: $i * 10px;
    }
    .rt-pt-#{$i* 10} {
        padding-top: $i * 10px;
    }
    .rt-spacer-#{$i* 10} {
        height: $i * 10px;
    }
}
.rt-mb-45 {
    margin-bottom: 45px;
}

@for $i from 10 through 60 {
    .rt-hw-#{$i} {
        height: $i + px;
        width: $i + px;
        line-height: $i + px;
    }
}
@media (max-width: 1600px){
    .rt-spacer-xl-0{
        height: 0px;
    }
    .rt-spacer-xl-50{
        height: 50px;
    }
}
@media (max-width: 1199.98px) {
    @for $i from 0 through 10 {
        .rt-mb-lg-#{$i* 10} {
            margin-bottom: $i * 10px;
        }
        .rt-pt-lg-#{$i* 10} {
            padding-top: $i * 10px;
        }
        .rt-spacer-lg-#{$i* 10} {
            height: $i * 10px;
        }
    }
}

@media (max-width: 991.98px) {
    @for $i from 0 through 10 {
        .rt-mb-md-#{$i* 10} {
            margin-bottom: $i * 10px;
        }
        .rt-pt-md-#{$i* 10} {
            padding-top: $i * 10px;
        }
        .rt-spacer-md-#{$i* 10} {
            height: $i * 10px;
        }
    }
}

@media (max-width: 767.98px) {
    @for $i from 0 through 10 {
        .rt-mb-xs-#{$i* 10} {
            margin-bottom: $i * 10px;
        }
        .rt-pt-xs-#{$i* 10} {
            padding-top: $i * 10px;
        }
        .rt-spacer-xs-#{$i* 10} {
            height: $i * 10px;
        }
    }
}

@import "custom_column";
