.single-rt-banner {
    @include afterparent();
    //background-color: var(--primary_color);
    @include breakpoint(xs) {
        background-image: none !important;
        background-color: var(--secondary_color);
    }
    .rt-inner-overlay {
        z-index: 0;
    }
    .rt-banner-content {
        @include breakpoint(xs) {
            padding-top: 60px;
        }
        h4 {
            font-size: 28px;
            font-weight: 600;
            line-height: 38px;
            color: $primary-color;
            text-transform: uppercase;
            @include breakpoint(xs) {
                font-size: 22px;
                line-height: 32px;
            }
        }
        h1 {
            font-size: 80px;
            font-weight: 800;
            line-height: 90px;
            text-transform: uppercase;
            margin-bottom: 0 0 6px;
            @include breakpoint(xs) {
                font-size: 32px;
                line-height: 42px;
            }
        }
        p {
            font-size: 24px;
            line-height: 38px;
            margin: 0 0 35px;
            @include breakpoint(xs) {
                font-size: 18px;
                line-height: 28px;
            }
        }
        .banner-check {
            margin-top: 15px;
        }

        [class*="icheck-material"] > input:first-child + input[type="hidden"] + label::before,
        [class*="icheck-material"] > input:first-child + label::before {
            border-radius: 50%;
        }

        [class*="icheck-material"] > input[type="checkbox"]:first-child:checked + input[type="hidden"] + label::after,
        [class*="icheck-material"] > input[type="checkbox"]:first-child:checked + label::after {
            width: 6px;
            height: 13px;
            transform: translate(8px, 3px) rotate(45deg);
        }
        
        .banner-domain-search{
            max-width: 635px;
        }
        .domain-searh-form{
            .serch-btn-group{
                flex: 0 0 115px;
                max-width: 115px;
                padding-right: 5px;
            }
            .domianinput-fileds{
                &::after{
                    right: 226px;
                }
                @include breakpoint(xs){
                    
                    flex-wrap: wrap;
                    height: auto;
                    padding-top: 10px;
                    padding-bottom: 10px;
                    border-radius: 10px;
                    .form-bdage{
                        top: 9px;
                        transform: none;
                    }
                    &::after{
                        right: 50%;
                        top: unset;
                        transform: none;
                        bottom: 20px;
                    }
                }
                
            }
            .domianinput-fileds input[type="email"], .domianinput-fileds input[type="text"]{
                flex: calc(100% - 250px);
                max-width: calc(100% - 250px);
                @include breakpoint(xs){
                    flex: 100%;
                    max-width: 100%;
                    display: block;
                    width: 100%;
                    padding-right: 15px;
                }
            }
            .domianinput-fileds .select-optin{
                margin-left: 37px;
                @include breakpoint(xs){
                    margin-left: 0px;
                    flex: 0 0 50%;
                    max-width: 50%;
                    .domianinput-fileds .select2-container--default .select2-selection--single .select2-selection__arrow::after {
                        right: 50px;
                    }
                }
            }
        }
    }
}

.rt-banner-area {
    .banner-services {
        margin-top: -67px;
        @include afterparent();
    }
}

.rt-banner-height {
    height: 930px;
    @include breakpoint(xs) {
        height: 700px;
    }
}

.rt-banner-height2 {
    height: 1085px;
}

.rt-banner-height3{
    height: 955px;
    @include breakpoint(xs){
        height: auto;
    }
}

.home-two {
    .single-rt-banner {
        @include breakpoint(md) {
            background-image: none !important;
            background-color: var(--secondary_color);
        }
    }
    .rt-banner-height2 {
        @include breakpoint(md) {
            height: 700px;
        }
        @include breakpoint(xs){
            height: 600px;
        }
    }
    .rt-banner-content {
        @include breakpoint(md) {
            padding-top: 100px;
        }
        @include breakpoint(lg) {
            h1 {
                br {
                    display: none;
                }
            }
        }
    }
}
.home-three{
    .rt-banner-content{
        h1{
            text-transform: capitalize;
        }
      
        @include breakpoint(xs){
            padding-top: 200px;
            padding-bottom: 50px;
            h1, p{
                color: white;
            }
            .smallgap.row > [class*="col-"]{
                padding-right: 15px !important;
                padding-left: 15px !important;
            }
        }
    }
}

.rt-slider-active {
    .slick-next,
    .slick-prev {
        position: absolute;
        right: 160px;
        @include breakpoint(xxl) {
            right: 0;
        }
        text-align: center;
        color: #000;
        z-index: 66;
        cursor: pointer;
        background-color: #fff;
        box-shadow: 0px 11px 29px 0px rgba(64, 65, 91, 0.11);
        border-radius: 50%;
        @include hw(50px);
        top: 50%;
        transform: translateX(-50%);
        opacity: 1;
        @include rt-trs(0.2s);
        &::before {
            color: #000;
            font-family: "icofont";
            opacity: 1;
            font-size: 25px;
            @include rt-trs(0.2s);
            line-height: 50px;
            @include breakpoint(xs) {
                font-size: 16px;
                line-height: 35px;
            }
        }
        @include breakpoint(xs) {
            @include hw(35px);
        }
        &:hover {
            background-color: var(--primary_color);
            &::before {
                opacity: 1;
                color: #fff;
            }
        }
    }
    .slick-prev {
        left: auto;
        margin-top: 60px;
        &::before {
            content: "\ea9d";
        }
    }
    .slick-next {
        &::before {
            content: "\eaa0";
        }
    }
}


.home-four{
    .rt-banner-content{
        background-color: rgba(255, 255, 255, 0.102);
        box-shadow: -7.234px -82.684px 10px 0px rgba(21, 28, 70, 0.1);
        padding: 30px;
        border-radius: 30px;
        @include breakpoint(xs){
            padding-top: 200px;
            padding-bottom: 50px;
            box-shadow: none;
            padding: 0px;
            background-color: transparent;
            h1, p{
                color: white;
            }
            .smallgap.row > [class*="col-"]{
                padding-right: 15px !important;
                padding-left: 15px !important;
            }
        }
    }
    
}